

body {
  background: #5f2c82;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #49a09d, #5f2c82);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #49a09d, #5f2c82); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  text-align: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.container {
  box-sizing: border-box;
	max-width: 1040px;

	width: 30%;
	margin: 0 auto;	
	padding: 20px;
  color: rgb(239, 239, 239);


}
.row {
  display: flex;
}


input::placeholder{
  color: rgba(247, 246, 246, 0.808);
  opacity: 1;
  font-weight: lighter;
}

input {
  color: white;

  border: none;
  border-radius: 0.4rem;            
  width: 100%;
  padding: 10px 6px;	
font-size: 16px;
  font-weight: 600;	
  text-transform: uppercase;
  text-align: center;

  background: transparent;
  border: 1px solid rgb(224, 224, 224);
  box-shadow: 2px 2px 10px rgba(230, 230, 230, 0.3);

}
input:focus {
  outline: none;
}
.card {
  margin-top: 20px;
    display:flex;    
    justify-content: center;
}
.card-container {
  margin:  20px 5px;
  border-radius: 20px;
  position: absolute;    
  width: 470px;
  height: 470px;
  background-color: #f2f4f6;    
  box-shadow: 0px 0px 25px 1px rgba(50, 50, 50, 0.1);       
}
.city-name {    
  font-weight:300;
  font-size:40px;
  color: #6b778d;
  bottom: 0;    
}
.icon {
  position: relative;
  width: 300px;
  height: 300px;
  bottom: 280px;
  float:right;
  margin: 5px 5px 0 0;    
}
.temp {
  position: relative;
  font-weight:300;
  font-size:80px;
  color:#17223b ;        
  right: 120px;
}



.card-footer {
  background-color: #16459d;     
  color: aliceblue;                
  position:relative;
  bottom: 280px;
  /* left: 0px; */
  border-radius: 0px 0px 20px 20px;        
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.temp-max-min {    
  font-weight:300;
  font-size:50px;
  color: aliceblue;    
  

}

@media (max-width: 1024px) {
  .container {
    width: 50%;
  }

 

  .card-container {
    width: 400px;
    height: 400px;
  }

  .temp {
    font-size: 60px;
  }

  .icon{
    width: 14rem;
    bottom: 230px;
  }
}

@media (max-width: 768px) {
  .container {
    width: 70%;
  }


  .card-container {
    width: 350px;
    height: 350px;
  }

  .temp {
    font-size: 50px;
  }
  .icon{
    width: 14rem;
    bottom: 230px;
  }
 
}

@media (max-width: 480px) {
  .container {
    width: 90%;
  }



  .card-container {
    width: 300px;
    height: 300px;
  }

  .temp {
    font-size: 40px;
    right: 4rem;
  }

  .city-name {
    font-size: 30px;
  }
  .icon{
    width: 9rem;
    bottom: 220px;
    right: 2rem;
  }
 
}


